@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap");
/* alerts  onloading animations */
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

/* Typography*/
html {
  font-size: 100%;
}

/*16px*/
body {
  display: grid;
  place-items: center;
  background: white;
  font-family: "IBM Plex Sans", sans-serif;
  line-height: 1.75;
  color: #191826;
}

p {
  font-weight: 400;
  max-width: 40em;
  margin-bottom: 1.5rem;
}
p.body1 {
  font-size: 1.25rem;
  /* (20px)*/
  line-height: 34px;
}
p.body2 {
  font-size: 1rem;
  /* (16px)*/
  line-height: 26px;
}

h1,
h2 {
  margin: 0;
  margin-bottom: 1.38rem;
  font-weight: 700;
  line-height: 1.3;
  text-transform: capitalize;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}
h1.primaryHeading {
  font-size: 88px;
  line-height: 88px;
  letter-spacing: -1px;
}

h2 {
  font-size: 2.441rem;
}

a {
  text-decoration: none;
}

/* buttons */
.btn {
  cursor: pointer;
  color: #fcfaf9;
  background: #191826;
  border: transparent;
  border-radius: 0.25rem;
  letter-spacing: 1px;
  padding: 0.375rem 0.75rem;
  transition: 0.3s ease-in-out all;
  text-transform: capitalize;
  display: inline-block;
  /*block class is good with forms*/
}
.btn:hover {
  background: #fcfaf9;
  color: #191826;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.btn-ios {
  color: #fcfaf9;
  background: #191826;
}
.btn-ios:hover {
  background: #66e2dc;
}
.btn-android {
  color: #191826;
  background: #fcfaf9;
}
.btn-android:hover {
  background: #ffb964;
}

/* alerts */

/* forms */

/* titles */
.title {
  text-align: center;
}

/* images */
.imgContainer {
  max-width: 100%;
}
.imgContainer .img {
  width: 100%;
  display: block;
  object-fit: cover;
}

.hero {
  display: grid;
  place-items: center;
  gap: 20px;
}
.hero .primaryHeading {
  font-size: 40px;
  line-height: 48px;
  letter-spacing: -0.45px;
  margin: 0;
}
.hero .body2 {
  margin: 0;
}

.card {
  width: 100%;
  display: grid;
  place-items: center;
  border-radius: 0.25rem;
  background-color: #191826;
  position: relative;
}
.card::before {
  content: "";
  width: 312px;
  height: 468px;
  position: absolute;
  top: 0;
  background: #191826 url(../assets/bg-pattern-2.svg) no-repeat center center/contain;
}
.card__header {
  transform: translateY(-100px);
  padding: 0 1.5rem;
}
.card__body {
  background-color: #fa7453;
  color: #fcfaf9;
}
.card__body .container {
  padding: 0 1.5rem;
}
.container {
  width: 100%;
  max-width: 1440px;
  display: grid;
  place-items: center;
  position: relative;
  gap: 64px;
}

.header {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  background-color: transparent;
  overflow: hidden;
  position: relative;
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  right: -25%;
  width: 100%;
  height: 100%;
  max-width: 1440px;
  padding: 0;
  background: url("../assets/bg-main-mobile.png") no-repeat left bottom;
  background-size: cover;
  background-blend-mode: normal;
  z-index: 9;
}
.header .container {
  padding: 0 1.5rem;
}

.nav {
  width: 100%;
  padding: 2.5rem 0;
  display: grid;
  place-items: center;
  justify-content: start;
}
.nav .imgContainer {
  width: 150px;
}

.main {
  max-width: 100%;
  display: grid;
  place-items: center;
  margin-top: 100px;
  border-radius: 0.75rem;
}

*,
::after,
::before {
  box-sizing: border-box;
}

